import uniNavBar from "../../components/uni-nav-bar/uni-nav-bar.vue"
export default {
	components: {
		uniNavBar
	},
	props: {
		tabIndex: Number,
		tabBars: Array
	},
	methods: {
		// 导航栏内容切换
		changeTab (index) {
			this.$emit('changeTab', index);
		},
		// 跳转发布
		openAdd () {
			this.user.navigate({
				url: '../add-input/add-input'
			});
		}
	}
}
